@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0 auto}.container{max-width:1000px;width:100%;padding:0.5rem}.Toolbar{display:flex;justify-content:flex-end}.Canvas{position:relative;background:radial-gradient(circle, #0057af 0%, #0050a0 100%);height:500px;display:flex;align-items:center;flex-direction:column;justify-content:space-around;padding:2rem 0}.Canvas__title{width:calc(100% - 4rem);line-height:1.5;text-align:center;font-size:28px;color:white;text-shadow:1px 2px 0px rgba(80,80,80,0.3);letter-spacing:1px;font-weight:900;font-family:'Roboto Slab'}.Canvas__subtitle{max-width:90%;width:auto;font-family:'Roboto';font-weight:700;border-radius:8px;background-color:white;padding:8px 12px;font-size:12px;letter-spacing:0.5px;display:flex}.Canvas__subtitle--redPart{color:#ee3333;white-space:nowrap}.Canvas__subtitle--greenPart{color:#50aa11;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.Canvas .watermark{opacity:0.2;color:white;font-family:'Roboto Slab';font-weight:500;position:absolute;bottom:8px;right:12px;font-style:italic}.Fields{margin:24px 0;display:flex;flex-direction:column}.Fields .field{margin-bottom:12px}@media screen and (min-width: 578px){.outer-container{display:flex;justify-content:center;align-items:center}.container{display:grid;grid-template-columns:auto 1fr;grid-template-rows:52px auto;grid-column-gap:1rem;margin-bottom:2rem}.Canvas{width:400px;height:600px;grid-row:1 / 2}.Toolbar{grid-column:2;grid-row:1;justify-content:flex-start}.Fields{grid-column:2;grid-row:2}}.emoji-mart{width:100% !important}

